.Home {
    display: flex;
    margin-top: 1cm;
    column-gap: 3cm;
    justify-content: center;
    align-items: flex-start;

}

.bio-box {
    display: flex;
    justify-content: center;
}

.backI {
    width: 9cm;
    height: auto;
    // position: absolute;
    // z-index: -1;
    // opacity: .8;
    border-radius: 100px;
}

.home-box {
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.bio {
    font-size: 25px;
    font-family: Titillium Web;
    margin-top: -1.2cm;
    align-items: center;
    line-height: .85cm;
    color: rgb(84, 132, 60);
}

.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    height: fit-content;
    background: linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    margin: 0;
    padding: 0;
    /* Add this line to remove padding */
}


  

  